    <template>
        <div>
            <table>
                <tr>
                    <td>用户名</td>
                    <td>
                        <input type="text" v-model="register_user">
                    </td>
                </tr>
                <tr>
                    <td>密码</td>
                    <td>
                        <input type="password" v-model="register_password" minlength=6 maxlength=12>
                    </td>
                </tr>
                <tr>
                    <td>确认密码</td>
                    <td>
                        <input type="password"  v-model="register_repeat_password" minlength=6 maxlength=12>
                    </td>
                </tr>
                <tr>
                    <td>
                        <button  @click="toregister">注册</button>
                    </td>
                    <td>
                        <p v-if="reg_confirm" v-text="msg"></p>
                    </td>
                </tr>
            </table>
        </div>
    </template>

    <script>
    import CONF from '../config'
    import axios from 'axios'

    export default {
        name:'signup',
        data() {
                    return {
                        register_user: "",
                        register_password: "",
                        register_repeat_password: "",
                        reg_confirm: false,
                        msg:""
                    }
                },
        methods: {
            toregister() {
                axios.get(`${CONF.BASEURL}/register`, {
                        params: {
                            register_user: this.register_user,
                            register_password: this.register_password,
                            register_repeat_password: this.register_repeat_password
                        }
                    })
                    .then(response=>{
                        if (response.data.state) {
                            this.$store.commit('set_user', this.register_user);
                        } else {
                            this.reg_confirm = true;
                            this.msg = response.data.data;
                        }
                    })
                    .catch(error=>{
                        this.reg_confirm = true;
                        this.msg = error;
                    });

            }
        }
    }
    </script>
    
